<template lang="html">
    <div class="comic-modal">
        <div class="comic-modal__container">
            <button class="comic-modal__close" @click="close">X</button>
            <div class="comic-modal__content">
                <img :src="thumbnailSrc">
                <div class="comic-modal__txt">
                    <h2>{{ title }}</h2>
                    <p v-html="description" v-if="description"></p>
                    <p v-else>No description...</p>
                </div>
            </div>
            <div class="comic-modal__nav">
                <button class="add-favourite" :class="{'add-favourite--disabled': favouriteExists(id)}" @click="addFavourite">{{ favouriteExists(id) ? 'added to favourites' : 'add to favorites' }}</button>
                <button class="buy">Buy for $ {{ price }}</button>
            </div>
        </div>
    </div>
</template>

<script>
import {mapMutations, mapGetters} from 'vuex';

export default {

    props: {
        id: {
            type: Number,
            required: true
        },
        title: String,
        description: String,
        thumbnail: Object,
        price: Number
    },

    computed: {

        ...mapGetters(['favouriteExists']),

        thumbnailSrc () {

            return `${this.thumbnail.path}.${this.thumbnail.extension}`;

        },

    },

    methods: {

        close () {

            this.$emit('close-modal');

        },

        addFavourite () {

            if (!this.favouriteExists(this.id)) {

                this.$store.commit('addFavourite', {
                    id: this.id,
                    title: this.title,
                    thumbnail: this.thumbnailSrc
                });

            }

        }

    }

}
</script>

<style lang="scss">
.comic-modal {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.7);

    .comic-modal__container {
        display: flex;
        flex-wrap: wrap;
        background-color: #fefefe;
        margin: 10% auto;
        height: 370px;
        width: 50%;
        position: relative;

        .comic-modal__close {
            color: #FFF;
            position: absolute;
            top: 10px;
            right: 10px;
            border: none;
            background-color: #2E3333;
            width: 35px;
            height: 35px;
            border-radius: 50%;
        }

        .comic-modal__content {
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;
            padding: 20px;

            img {
                width: 30%;
            }

            .comic-modal__txt {
                padding-left: 20px;
                width: 70%;

                h2 {
                    color: #2E3333;
                    text-transform: uppercase;
                }

                p {
                    color: #7F8C8D;
                    width: 100%;
                    display: -webkit-box;
                    overflow: hidden;
                    -webkit-line-clamp: 10;
                    -webkit-box-orient: vertical;
                }
            }
        }

        .comic-modal__nav {
            display: flex;
            align-self: flex-end;
            height: 45px;
            width: 100%;

            button {
                width: 50%;
                border: none;
                text-transform: uppercase;
                font-weight: bold;

                &.add-favourite,
                &.buy {
                    background-repeat: no-repeat;
                    background-position: 10% center;
                    background-size: 30px 30px;
                }

                &.add-favourite {
                    color: #2E3333;
                    background-color: lighten(#BDC3C7, 10%);
                    background-image: url('data:image/png;base64,');

                    &--disabled {
                        background-image: url('data:image/png;base64,');
                        background-color: #2E3333;
                        color: #F2282E;
                    }
                }

                &.buy {
                    color: #F2282E;
                    background-color: #BDC3C7;
                    background-image: url('data:image/png;base64,');
                }
            }
        }
    }
}
</style>



